import styled from 'styled-components';

const StyledPlayList = styled.div`
    flex: 1 1 450px;
    overflow: hidden;
    color: white;
    padding-top: 50px;
    
    @media screen and (max-width: 1400px) {
    width: 100%;
    display: block;
    }
`;

export default StyledPlayList;
